import React, { Fragment } from "react";

export default class Home extends React.Component {
	constructor() {
		super();
		this.state = { name: "" };
		this.clickHandler = this.clickHandler.bind(this);
	}

	clickHandler() {
		this.setState({ name: "(   )" });
	}

	render() {
		return (
			<Fragment>
				<h1 onClick={this.clickHandler}>{`, ${this.state.name}!`}</h1>
				<h3>
					   Quest,      
					
				</h3>
			</Fragment>
		);
	}
}
